<template>
  <div>
    <v-toolbar dense height="60px" color="error">
      <v-toolbar-title>
        <router-link to="/">
          <v-btn icon><v-icon size="35" color="white">mdi-home</v-icon></v-btn>
        </router-link>
      </v-toolbar-title>
    </v-toolbar>
  <v-container fluid class="bg-light">
    <v-row align-content="center">
      <v-col cols="12" lg="7">
        <div v-if="getimage.reseller_reg_img !== null">
          <img :src=url+getimage.reseller_reg_img class="img-fluid">
        </div>
        <div v-else>
          <img src="@/assets/reseller-register.jpg" class="img-fluid">
        </div>

      </v-col>
      <v-col cols="12" lg="5">
        <v-layout align-center justify-center>
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-card class="elevation-18">
              <v-toolbar flat color="grey">
                <v-toolbar-title class="mx-auto text-white">Register As Seller</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-text-field type="number" v-model="businessform.user_role" class="d-none"></v-text-field>
                <v-text-field type="number" v-model="businessform.user_status" class="d-none"></v-text-field>
                <v-row align-content="center">
                  <v-col cols="12" md="6">
                    <v-text-field
                        v-model="businessform.name"
                        :rules="nameRules"
                        :counter="50"
                        label="Your Name"
                        type="text"
                        prepend-inner-icon="mdi-account">
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-select
                        v-model="businessform.gender"
                        :rules="genderRules"
                        label="Select Gender"
                        :items="genders"
                        item-text="gender"
                        item-value="gender"
                        prepend-inner-icon="mdi-human">
                    </v-select>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12">
                    <v-text-field
                        v-model="businessform.email"
                        :rules="emailRules"
                        @keyup="emailVerify"
                        :error-messages="emailVerifyMessage"
                        label="Your Email"
                        type="email"
                        prepend-inner-icon="mdi-email">
                    </v-text-field>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12" md="6">
                    <v-text-field class=""
                        v-model="businessform.password"
                        :rules="passwordRules" 
                        label="Enter Password"
                        type="password"
                        :counter="15"
                        prepend-inner-icon="mdi-lock">
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-text-field
                        v-model="businessform.confirm_password"
                        label="Confirm Password"
                        type="password" :rules="[(businessform.password === businessform.confirm_password) || 'Confirm password does not match']"
                        :counter="15"
                        prepend-inner-icon="mdi-lock">
                    </v-text-field>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12" md="6">
                    <v-autocomplete
                        v-model="businessform.profession_id"
                        :rules="professionRules"
                        label="Select Profession"
                        :items="allProfessions"
                        item-text="profession"
                        item-value="id"
                        prepend-inner-icon="mdi-tray-arrow-down">
                    </v-autocomplete>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-autocomplete
                        v-model="businessform.skill_id"
                        :rules="skillRules"
                        label="Skills & Profession"
                        :items="allSkills"
                        item-text="skill"
                        item-value="id"
                        prepend-inner-icon="mdi-code-tags">
                    </v-autocomplete>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12" md="6">
                    <v-text-field
                        v-model="businessform.mobile_phone"
                        :rules="mobileRules" 
                        @keyup="phoneVerify"
                        :error-messages="phoneVerifyMessage"
                        label="Enter Mobile"
                        type="number"
                        :counter="10"
                        prepend-inner-icon="mdi-cellphone">
                    </v-text-field>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-text-field
                        v-model="businessform.date_of_birth"
                        :rules="dateRules"
                        type="date"
                        label="Enter DOB"
                        prepend-inner-icon="mdi-calendar">
                    </v-text-field>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12" md="6">
                    <v-autocomplete
                         v-model="stateId"
                         @change="getCities"
                        :items="allstates"
                        :rules="stateRules"
                        label="Select State"
                        item-text="state"
                        item-value="id"
                        prepend-inner-icon="mdi-home-city">
                    </v-autocomplete>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-autocomplete
                      v-model="businessform.city_id"
                      :rules="cityRules"
                      :items="cities"
                      label="Select City"
                      :no-data-text="customNoDataText"
                      item-text="city"
                      item-value="id"
                      prepend-inner-icon="mdi-city" >
                    </v-autocomplete>
                  </v-col>
                </v-row>
                <v-row align-content="center">
                  <v-col cols="12" md="12">
                    <v-textarea class=""
                        v-model="businessform.address"
                        :rules="addressRules"
                        :counter="100"
                        label="Enter Address"
                        rows="2"
                        prepend-inner-icon="mdi-map">
                    </v-textarea>
                  </v-col>
                </v-row>
                <v-row class="mt-0">
                  <v-col cols="12" md="12">
                    <v-checkbox type="checkbox" value="1" class="my-0 py-0"
                        v-model="businessform.agree_policy"
                        :rules="agree_policyRules">
                      <template v-slot:label>
                        <div>
                          Agree to
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                              <a
                                  target="_blank"
                                  href="/terms-of-use"
                                  @click.stop
                                  v-on="on" class="vcart-right-section">
                                Terms of Use
                              </a>
                            </template>
                            Opens in new window
                          </v-tooltip>
                        </div>
                      </template>
                    </v-checkbox>
                  </v-col>
                </v-row>
              </v-card-text>
              <v-card-actions class="cart-btn-btm">
                <v-btn :disabled="!valid" @click="ResellerformSubmit" color="red" large block class="text-white">Create</v-btn>
              </v-card-actions>
            </v-card>
          </v-form>

          <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
          <template v-slot:action="{}">
            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
          </template>
        </v-snackbar>
        </v-layout>
      </v-col>
    </v-row>
  </v-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "ResellerRegister",
  metaInfo: {title: 'Reseller Register'},
  data() {
    return{
      businessform:{
        name: '',
        gender: '',
        email:'',
        password: '',
        confirm_password:'',
        profession_id: '',
        skill_id: '',
        mobile_phone: '',
        date_of_birth: '',
        state_id: '',
        city_id: '',
        address: '',
        agree_policy: '',
        user_role: 2,
        user_status: 1,
      },
      stateId: '',
      allstates:[],
      getimage:[],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
      cities:[],
      allProfessions:[],
      allSkills:[],
      emailVerifyMessage:'',
      phoneVerifyMessage:'',
      customNoDataText: 'Please Select State First',
      valid: false,
      snackbar: false,
      text: '',
      timeout: '',
      genders:[{id:1,gender:'Male'},{id:2,gender:'Female'}],
      nameRules: [
        v => !!v || 'Name is required.',
        v => v.length <= 50 || 'Name must be less than 10 characters.',
      ],
      genderRules: [
        v => !!v || 'Gender is required.',
      ],
      emailRules: [
        v => !!v || 'E-mail is required.',
        v => /.+@.+/.test(v) || 'E-mail must be valid.',
      ],
      
      passwordRules: [
        v => !!v || 'Password is required.',
        v => /[A-Z]/.test(v) || 'Use upper case.',
        v => /[a-z]/.test(v) || 'Use lower case.',
        v => /[0-9]/.test(v) || 'Use number.',
        v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
        v => v.length >= 8 || 'Password must be greater than 8 characters.',
        v => v.length <= 15 || 'Password must be less than 15 characters.',
      ],
      
      professionRules: [
        v => !!v || 'Profession is required.',
      ],
      skillRules: [
        v => !!v || 'Skill is required.',
      ],

      mobileRules: [
        v => !!v || 'Mobile number is required.',
        v => v.length == 10 || 'Mobile number must be 10 characters.',
      ],
      dateRules: [
        v => !!v || 'DOB is required.',
      ],
      
      stateRules: [
        v => !!v || 'State is required.',
      ],
      cityRules: [
        v => !!v || 'City is required.',
      ],
      addressRules: [
        v => !!v || 'Address is required.',
        v => v.length <= 100 || 'Address must be less than 100 characters.',
      ],
      agree_policyRules: [
        v => !!v || 'This field is required.',
      ],
    } 
  },

  mounted() {
      this.getbusinessimage();
      this.getCities();
      this.All_States();
      this.All_Professions();
      this.All_skills();
  },

  methods: {
        getbusinessimage(){
          axios.get('profile/details')
              .then((resp)=>{
                this.getimage = resp.data.profile_details;
              });
        },

        All_Professions(){
          axios.get('professions')
            .then((resp)=>{
              this.allProfessions = resp.data.professions;
            });
        },

        All_skills(){
          axios.get('skills')
            .then((resp)=>{
              this.allSkills = resp.data.skills;
            });
        },
        All_States(){
          axios.get('allstates')
            .then((resp)=>{
              this.allstates = resp.data.locations;
            });
        },
       getCities() {
         if (this.stateId) {
           axios.get(`states/${this.stateId}`)
               .then(response => {
                 this.cities = response.data;
               })
               .catch(error => {
                 console.log(error);
               });
         } else {
           this.cities = [];
         }
       },
        emailVerify(){ // verify email
          if(/.+@.+/.test(this.businessform.email)){
              axios.post('user/verify', {'email':this.businessform.email})
                  .then((resp) => {
                    if(resp.data.status==400){
                        this.emailVerifyMessage = resp.data.message;
                    }else{
                        this.emailVerifyMessage = "";
                    }
              });
            }
        },
        phoneVerify(){ // verify phone
            if(this.businessform.mobile_phone.length == 10){
                axios.post('user/verify', {'mobile_phone':this.businessform.mobile_phone})
                    .then((resp) => {
                      if(resp.data.status==400){
                          this.phoneVerifyMessage = resp.data.message;
                      }else{
                          this.phoneVerifyMessage = "";
                      }
                });
            }
        },
    
       async ResellerformSubmit() {
          const config = {
            headers: { 'content-type': 'multipart/form-data' }
          }
          await axios.post('reseller/register', this.businessform, config)
              .then((resp) => {
                this.snackbar = true;
                this.text = resp.data.message;
                this.timeout = 3000;
                this.$router.push({name: 'resellerlogin'});
              }).catch(error => {
                // var data = {...error.response};
                var data = error.toJSON();
                if(data.status == 400){
                  this.snackbar = true;
                  this.text = "something went wrong.";
                  this.timeout = 3000;
                }
              });
          this.$refs.form.validate();
          // console.warn("Form submit = ");
        },
        
    }
}
</script>

<style scoped>
  .v-card__text .col {
    padding: 0 5px;
  }
</style>